body {
  margin: 0;
  font-family: "montseratt",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  background-image: url("./assets/cold-bg.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.App.warm {
  background-image: url("./assets/warm-bg.jpg");
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

.search-box {
  width: 100%;
  margin-bottom: 75px;
}

.search-box .search-bar {
  display: block;
  width: 90%;
  padding: 17px;
  margin: auto;

  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(
    255,
    255,
    255,
    0.5
  );
  border-radius: 0px 0px 16px
    16px;
  margin-top: -25px;
  box-shadow: 0px 5px
    rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.04s ease;
}

.search-box
  .search-bar:focus {
  background-color: rgba(
    255,
    255,
    255,
    0.7
  );
}

.location-box .location {
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-shadow: 3px 3px
    rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-shadow: 2px 2px
    rgba(50, 50, 70, 0.5);
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(
    255,
    255,
    255,
    0.2
  );
  border-radius: 16px;
  padding: 15px 25px;
  font-size: 102px;
  color: white;
  font-weight: 900;
  text-shadow: 3px 6px
    rgba(50, 50, 70, 0.5);
  box-shadow: 3px 6px
    rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px
    rgba(50, 50, 70, 0.5);
}
